import React, { useState, useEffect, useCallback, useRef } from 'react';
import '../styles/SpeechToText.css';
import axios from 'axios';

const SpeechToText = ({ onTranscript, userId }) => {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState('');
  const [error, setError] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [retryCount, setRetryCount] = useState(0);
  const [isRecognitionActive, setIsRecognitionActive] = useState(false);
  const [connectionState, setConnectionState] = useState('disconnected'); // 'disconnected', 'connecting', 'connected', 'error'
  
  const recognitionRef = useRef(null);
  const retryTimeoutRef = useRef(null);
  const abortCountRef = useRef(0); // Track consecutive aborts
  const isComponentMounted = useRef(true);
  const MAX_RETRIES = 2; // Reduced from 3 to 2
  const MAX_CONSECUTIVE_ABORTS = 1; // Reduced from 2 to 1
  const RETRY_DELAY = 1000;
  const NETWORK_RETRY_DELAY = 500;
  const ABORT_RETRY_DELAY = 300;

  // Setup recognition instance (moved above its first use)
  const setupRecognition = useCallback(() => {
    if (!isComponentMounted.current || !userId) {
      console.log('Cannot setup recognition: component unmounted or no user');
      return null;
    }

    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) {
      setError('Speech Recognition API is not supported in this browser. Please use Chrome or Edge.');
      return null;
    }

    // Cleanup existing recognition if any
    cleanup();

    const recognition = new SpeechRecognition();
    recognition.continuous = true;
    recognition.interimResults = true;
    recognition.lang = 'en-US';

    recognition.onstart = () => {
      if (!isComponentMounted.current) return;
      console.log('Recognition started');
      setConnectionState('connected');
      setError(null);
      abortCountRef.current = 0;
    };

    recognition.onresult = async (event) => {
      if (!isComponentMounted.current) return;
      let interimTranscript = '';
      let finalTranscript = '';

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcriptPart = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          finalTranscript += transcriptPart;
        } else {
          interimTranscript += transcriptPart;
        }
      }

      const currentTranscript = finalTranscript + interimTranscript;
      setTranscript(currentTranscript);

      if (finalTranscript) {
        try {
          setIsProcessing(true);
          const response = await axios.post('http://localhost:5001/api/speech', 
            { text: finalTranscript },
            { 
              headers: {
                'Content-Type': 'application/json',
                'X-User-ID': userId
              },
              timeout: 5000 // Add timeout to prevent hanging requests
            }
          );
          
          if (onTranscript) {
            onTranscript(finalTranscript, response.data);
          }
        } catch (err) {
          console.error('Error processing transcript:', err);
          if (err.code === 'ECONNABORTED') {
            setError('Request timed out. Please try again.');
          } else {
            setError(err.response?.data?.error || 'Failed to process speech. Please try again.');
          }
        } finally {
          setIsProcessing(false);
        }
      }
    };

    recognition.onerror = (event) => {
      if (!isComponentMounted.current) return;
      console.error('Speech recognition error:', event.error);
      
      let errorMessage = '';
      let shouldRetry = false;
      let retryDelay = RETRY_DELAY;
      
      switch (event.error) {
        case 'network':
          if (retryCount >= MAX_RETRIES) {
            errorMessage = 'Network error occurred. Please try again later.';
            shouldRetry = false;
            setIsListening(false);
            cleanup();
          } else {
            errorMessage = 'Network error occurred. Retrying...';
            shouldRetry = true;
            retryDelay = NETWORK_RETRY_DELAY;
          }
          setConnectionState('error');
          break;
        case 'aborted':
          abortCountRef.current += 1;
          if (abortCountRef.current >= MAX_CONSECUTIVE_ABORTS) {
            errorMessage = 'Speech recognition was interrupted. Please try again.';
            shouldRetry = false;
            setIsListening(false);
            cleanup();
          } else {
            errorMessage = 'Speech recognition was interrupted. Retrying...';
            shouldRetry = true;
            retryDelay = ABORT_RETRY_DELAY;
          }
          setConnectionState('error');
          break;
        case 'not-allowed':
          errorMessage = 'Microphone access was denied. Please allow microphone access and try again.';
          setConnectionState('error');
          break;
        case 'audio-capture':
          errorMessage = 'No microphone was found. Please ensure a microphone is connected.';
          setConnectionState('error');
          break;
        case 'no-speech':
          errorMessage = 'No speech was detected. Please try speaking again.';
          shouldRetry = true;
          break;
        case 'service-not-allowed':
          errorMessage = 'Speech recognition service is not allowed. Please check your browser settings.';
          setConnectionState('error');
          break;
        default:
          errorMessage = `Speech recognition error: ${event.error}. Please try again.`;
          shouldRetry = retryCount < MAX_RETRIES;
          setConnectionState('error');
      }
      
      setError(errorMessage);
      
      if (shouldRetry && isListening && isComponentMounted.current) {
        setRetryCount(prev => prev + 1);
        setConnectionState('connecting');
        
        cleanupTimeouts();
        
        retryTimeoutRef.current = setTimeout(() => {
          if (isListening && !isRecognitionActive && isComponentMounted.current) {
            if (safeStartRecognition()) {
              setError('Reconnecting...');
            } else {
              setError('Failed to reconnect. Please try again.');
              setIsListening(false);
              cleanup();
            }
          }
        }, retryDelay);
      } else if (!shouldRetry) {
        setIsListening(false);
        cleanup();
      }
    };

    recognition.onend = () => {
      if (!isComponentMounted.current) return;
      console.log('Recognition ended');
      setIsRecognitionActive(false);
      
      if (isListening && isComponentMounted.current) {
        if (retryCount < MAX_RETRIES && abortCountRef.current < MAX_CONSECUTIVE_ABORTS) {
          setConnectionState('connecting');
          cleanupTimeouts();
          
          retryTimeoutRef.current = setTimeout(() => {
            if (isListening && !isRecognitionActive && isComponentMounted.current) {
              if (!safeStartRecognition()) {
                setError('Failed to restart speech recognition. Please try again.');
                setIsListening(false);
                cleanup();
              }
            }
          }, RETRY_DELAY);
        } else {
          setError('Maximum retry attempts reached. Please try again.');
          setIsListening(false);
          cleanup();
        }
      } else {
        setConnectionState('disconnected');
      }
    };

    return recognition;
  }, [isListening, onTranscript, userId, retryCount, isRecognitionActive, cleanup, safeStartRecognition]);

  // Cleanup function to clear any pending timeouts and recognition
  const cleanup = useCallback(() => {
    console.log('Cleaning up speech recognition...');
    if (retryTimeoutRef.current) {
      clearTimeout(retryTimeoutRef.current);
      retryTimeoutRef.current = null;
    }
    if (recognitionRef.current) {
      try {
        recognitionRef.current.stop();
      } catch (err) {
        console.warn('Error stopping recognition during cleanup:', err);
      }
      recognitionRef.current = null;
    }
    setIsRecognitionActive(false);
    setConnectionState('disconnected');
    setRetryCount(0);
    abortCountRef.current = 0;
    setError(null);
    setTranscript('');
  }, []);

  // Effect for cleanup on unmount
  useEffect(() => {
    return () => {
      console.log('Component unmounting, cleaning up...');
      isComponentMounted.current = false;
      cleanup();
    };
  }, [cleanup]);

  // Effect to handle userId changes (logout)
  useEffect(() => {
    if (!userId) {
      console.log('User logged out, cleaning up speech recognition...');
      cleanup();
      setIsListening(false);
    }
  }, [userId, cleanup]);

  // Cleanup function to clear any pending timeouts
  const cleanupTimeouts = useCallback(() => {
    if (retryTimeoutRef.current) {
      clearTimeout(retryTimeoutRef.current);
      retryTimeoutRef.current = null;
    }
  }, []);

  // Function to safely stop recognition
  const safeStopRecognition = useCallback(() => {
    cleanup();
  }, [cleanup]);

  // Function to safely start recognition (add cleanupTimeouts to deps)
  const safeStartRecognition = useCallback(() => {
    if (!isComponentMounted.current || !userId) {
      console.log('Cannot start recognition: component unmounted or no user');
      return false;
    }

    if (!recognitionRef.current) {
      const recognition = setupRecognition();
      if (!recognition) return false;
      recognitionRef.current = recognition;
    }

    try {
      recognitionRef.current.start();
      setIsRecognitionActive(true);
      setConnectionState('connected');
      abortCountRef.current = 0;
      return true;
    } catch (err) {
      console.error('Error starting recognition:', err);
      setConnectionState('error');
      setIsRecognitionActive(false);
      return false;
    }
  }, [userId, setupRecognition, cleanupTimeouts]);

  useEffect(() => {
    if (isListening) {
      const recognition = setupRecognition();
      if (recognition) {
        setConnectionState('connecting');
        if (!safeStartRecognition()) {
          setError('Failed to start speech recognition. Please try again.');
          setIsListening(false);
        }
      }
    }

    return () => {
      cleanupTimeouts();
      safeStopRecognition();
      setRetryCount(0);
      setConnectionState('disconnected');
    };
  }, [isListening, setupRecognition, safeStartRecognition, safeStopRecognition, cleanupTimeouts]);

  const toggleListening = () => {
    if (!userId) {
      setError('Please log in to use speech recognition');
      return;
    }
    
    if (isListening) {
      cleanup();
      setIsListening(false);
    } else {
      cleanup(); // Clear any existing state
      setIsListening(true);
      setRetryCount(0);
      setConnectionState('connecting');
    }
  };

  return (
    <div className="speech-to-text">
      <button
        onClick={toggleListening}
        className={`mic-button ${isListening ? 'active' : ''} ${isProcessing ? 'processing' : ''} ${connectionState}`}
        disabled={isProcessing}
        title={isListening ? 'Stop listening' : 'Start listening'}
      >
        <i className={`fas fa-microphone ${isListening ? 'fa-beat' : ''}`}></i>
        {isListening ? ' Stop' : ' Start'} Listening
        {connectionState === 'connecting' && ' (Connecting...)'}
      </button>
      {transcript && (
        <div className="transcript-container">
          <p className="transcript">
            <span className="label">Transcript:</span> {transcript}
          </p>
        </div>
      )}
      {error && (
        <div className="error-container">
          <p className="error">
            <i className="fas fa-exclamation-circle"></i> {error}
          </p>
        </div>
      )}
    </div>
  );
};

export default SpeechToText;